<template>
  <div>
    <div class="top-bar-tools">
      <v-btn @click="dialogDelete = true, isDeleteAll = false" :disabled="selected.length === 0" x-small color="error" class="white--text">
          Delete <v-icon x-small right dark > mdi-delete </v-icon>
      </v-btn>&nbsp;
      <v-btn @click="dialogDelete = true, isDeleteAll = true" x-small color="error" class="white--text"> Delete all </v-btn>
      <export-data :data="logs" :file-name="'error-logging'"></export-data>
    </div>

    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="logs"
      class="elevation-1"
      item-key="time"
      show-select
      hide-default-footer
      disable-sort>
    </v-data-table>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="()=>dialogDelete = false" :on-confirm="confirmDelete" dialog-text="Are you sure?"></confirm-dialog>

  </div>
</template>

<script>

import * as localstorageUtil from "../utils/localstorage";
import ExportData from './Common/ExportData.vue';
import ConfirmDialog from '../components/Common/ConfirmDialog';

export default {
  components: {
    ExportData, ConfirmDialog,
  },
  data:()=>({
    logs: [],
    selected: [],
    headers: [
      { text: "Message", value: "message", width: '50%'},
      { text: "Description", value: "description"},
    ],
    dialogDelete: false,
    isDeleteAll: false,
  }),

  methods: {
    deleteAll() {
      localstorageUtil.removeItem("logs");
      this.logs = [];
    },
    deleteSelected() {
      this.logs = this.logs.filter(log => !this.selected.includes(log));
      localstorageUtil.setItem('logs', JSON.stringify(this.logs));
    },
    confirmDelete(){
      this.isDeleteAll ? this.deleteAll() : this.deleteSelected();
      this.dialogDelete = false;
    },
  },
  mounted() {
    const logs = JSON.parse(localstorageUtil.getItem('logs')) || [];
    this.logs = logs;
  }
};
</script>
<style scoped>
.top-bar-tools {
  margin-bottom: 10px;
  text-align: left;
}
</style>
