<template>
  <v-menu offset-y>
    <template v-slot:activator="{ on, attrs }">
      <v-btn x-small color="#009688" class="white--text mr-2" dark v-bind="attrs" v-on="on">
        <span>Download</span>
        <v-icon x-small right dark>mdi-download</v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item link>
        <v-list-item-title @click="clickDownload(data, 'json')">Json</v-list-item-title>
      </v-list-item>
      <v-list-item link>
        <v-list-item-title @click="clickDownload(data, 'excel')">Excel</v-list-item-title>
      </v-list-item>
      <v-list-item link>
        <v-list-item-title @click="clickDownload(data, 'csv')">CSV</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-menu>
</template>
<script>
import { exportJSON, exportCSV, exportExcel } from '../../utils/utils';

export default {
  name: 'export-data',
  props: ['data', 'fileName', 'formatDataService'],
  methods: {
    clickDownload(data, type) {
      let formattedData = data;
      if (typeof this.formatDataService === 'function') {
        formattedData = this.formatDataService(data);
      }
      switch (type) {
        case 'csv':
          return exportCSV(this.fileName, formattedData);
        case 'excel':
          return exportExcel(this.fileName, formattedData);
        case 'json':
          return exportJSON(this.fileName, JSON.stringify(formattedData));
      }
    },
  },
};
</script>
